<template>
  <nav class="wrapper__nav">
    <div class="container wrapper__navbar">
      <a href="#!" class="logo">
        <img src="../assets/img/logo.png" alt="" />
        dBurger
      </a>
      <div class="wrapper__navbar-right">
        <button class="wrapper__navbar-btn" @click="isShovBasket = true">
          <img src="../assets/img/shopicon.svg" alt="" />
          <span
            :class="
              isCountBurgerOption
                ? 'wrapper__navbar-count active'
                : 'wrapper__navbar-count'
            "
            >{{ allCountProduct }}</span
          >
        </button>
        <div
          :class="
            isShovBasket
              ? 'wrapper__navbar-basket active'
              : 'wrapper__navbar-basket'
          "
        >
          <div class="wrapper__navbar-top">
            Корзина
            <button
              class="wrapper__navbar-close"
              @click="isShovBasket = false"
            ></button>
          </div>
          <div class="wrapper__navbar-checlist">
            <div
              class="wrapper__navbar-product"
              v-for="item in productBasketArr"
            >
              <div class="wrapper__navbar-info">
                <img
                  :src="item.img"
                  class="wrapper__navbar-productImage"
                  alt=""
                />
                <div class="wrapper__navbar-infoSub">
                  <p class="wrapper__navbar-infoName">{{ item.name }}</p>
                  <p class="wrapper__navbar-infoPrice">{{ item.totalPrice }}</p>
                </div>
              </div>
              <div class="wrapper__navbar-option">
                <button
                  class="wrapper__navbar-symbol fa-minus"
                  @click="
                    $emit('productBasketMinus', item.name),
                      $emit('totalPriceBasket'),
                      $emit('allTotalPrice');
                  "
                >
                  -
                </button>
                <output class="wrapper__navbar-amout">{{ item.count }}</output>
                <button
                  class="wrapper__navbar-symbol fa-plus"
                  @click="
                    $emit('productBasketPlus', item.name),
                      $emit('totalPriceBasket'),
                      $emit('allTotalPrice');
                  "
                >
                  +
                </button>
              </div>
            </div>
          </div>
          <div class="wrapper__navbar-botom">
            Заказать
            <span class="wrapper__navbar-totalprice">{{
              allTotalPricePr
            }}</span>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: "Navbar",
  props: {
    productBasketArr: Array,
    isCountBurgerOption: Boolean,
    allCountProduct: Number,
    allTotalPricePr: Number,
  },
  data() {
    return {
      isShovBasket: false,
    };
  },
};
</script>
