<template>
	<div class="container wrapper__list">
		<ProductItem 
		@productCountPlus="$emit('productCountPlus',productObj.name)"
		v-for="productObj in productArr" :product-obj="productObj"
		@isCountBurgerOp="$emit('isCountBurgerOp',$event)"
		@allCount="$emit('allCount')"
		@totalPriceBasket="$emit('totalPriceBasket')"
		@allTotalPrice="$emit('allTotalPrice')"
		/>
	</div>
</template>

<script>
import ProductItem from './ProductItem.vue';
	export default {
		name:'Productlist',
		components:{ProductItem},
		props:{
			productArr:Array,
		}
	}
</script>
