<template>
  <div class="wrapper__list-card">
    <p
      :class="
        isCountBurger ? 'wrapper__list-count active' : 'wrapper__list-count'
      "
    >
      {{ productObj.count }}
    </p>
    <img :src="productObj.img" class="wrapper__list-image" alt="" />
    <h3 class="wrapper__list-title">{{ productObj.name }}</h3>
    <div class="wrapper__list-sub">
      <p class="wrapper__list-price">{{ productObj.price }} сум</p>
      <button
        class="wrapper__list-btn"
        @click="
          (isCountBurger = true),
            ($emit('productCountPlus'), productObj.name),
            $emit('isCountBurgerOp', isCountBurger),
            $emit('allCount'),
            $emit('totalPriceBasket'),
            $emit('allTotalPrice')
        "
      >
        <img src="../assets/img/sellicon.svg" alt="" />
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProductItem",
  props: {
    productObj: Object,
  },
  data() {
    return {
      isCountBurger: false,
    };
  },
};
</script>
