<template>
  <div>
    <Navbar
      :is-count-burger-option="isCountBurgerOption"
      :product-basket-arr="productBasketArr"
      :all-total-price-pr="allTotalPrice"
      :all-count-product="allCountProduct"
      @productBasketMinus="productBasketMinus"
      @productBasketPlus="productBasketPlusFu"
      @totalPriceBasket="totalPriceBasket"
      @allTotalPrice="allTotalPriceFu"
    />
    <Productlist
      @productCountPlus="productCountPlus"
      :product-arr="productArr"
      @isCountBurgerOp="isCountBurgerOp"
      @allCount="allCountFu"
      @totalPriceBasket="totalPriceBasket"
      @allTotalPrice="allTotalPriceFu"
    />
  </div>
</template>

<script>
import { productArr2 } from "./items";
import Navbar from "./components/Navbar.vue";
import Productlist from "./components/Productlist.vue";
export default {
  name: "App",
  components: { Navbar, Productlist },
  data() {
    return {
      productBasketArr: [],
      productArr: productArr2,
      isCountBurgerOption: null,
      allCountProduct: 0,
      allTotalPrice: 0,
    };
  },
  methods: {
    productCountPlus(vf) {
      this.productArr.forEach((item) => {
        if (vf == item.name) {
          item.count++;
          this.productBasketArr.push(item);
        }
      });
      console.log(vf);
      console.log(this.productArr);
      console.log(this.allCountProduct);
    },
    isCountBurgerOp(event) {
      this.isCountBurgerOption = event;
      console.log(this.isCountBurgerOption);
    },
    allCountFu() {
      if (this.productBasketArr.length > 0) {
        let count4 = this.allCountProduct;
        for (const key in this.productBasketArr) {
          this.allCountProduct =
            count4 +
            (this.productBasketArr[key].count +
              1 -
              this.productBasketArr[key].count);
        }
        return this.allCountProduct;
      } else {
        this.allCountProduct = 0;
      }
    },
    totalPriceBasket() {
      this.productArr.forEach((item) => {
        item.totalPrice = item.price * item.count;
      });
    },
    productBasketMinus(name) {
      this.productArr.forEach((item) => {
        if (name == item.name) {
          if (item.count > 1) {
            item.count--;
          }
        }
      });
    },
    productBasketPlusFu(name2) {
      this.productArr.forEach((item) => {
        if (name2 == item.name) {
          item.count++;
        }
      });
    },
    allTotalPriceFu() {
      /* this.productBasketArr.map((item) => {
        this.allTotalPrice += item.totalPrice / item.count;
      }); */
      if (this.productBasketArr.length > 0) {
        let totaPrice4 = this.allTotalPrice;
        for (const key in this.productBasketArr) {
          this.allTotalPrice =
            totaPrice4 + this.productBasketArr[key].totalPrice;
        }
        return this.allTotalPrice;
      } else {
        this.allTotalPrice = 0;
      }
    },
  },
};
</script>
